@tailwind base;
@tailwind components;
@tailwind utilities;
.loader--hide {
  opacity: 0; }

/* Loader Animation */
@keyframes loading {
  0% {
    opacity: 0.4; }
  50% {
    opacity: 1; }
  100% {
    opacity: 0.4; } }

.header-loader {
  position: relative;
  background-size: 100%;
  animation: loading 2s infinite;
  background-repeat: repeat-y;
  background-image: linear-gradient(#ddd 3rem, transparent 0), linear-gradient(#ddd 3rem, transparent 0);
  height: 1rem;
  width: 100%; }

.panel-loader {
  position: relative;
  top: 50px;
  background-position: 5.25rem 2.25rem, 5.25rem 4.25rem, 5.25rem 6.25rem, 5.25rem 8.25rem, 5.25rem 10.25rem;
  background-size: 40% 100%, 0 100%, 70% 100%, 80% 100%, 30% 100%;
  animation: loading 2s infinite;
  background-repeat: repeat-y;
  background-image: linear-gradient(#ddd 1rem, transparent 0), linear-gradient(#ddd 1rem, transparent 0), linear-gradient(#ddd 1rem, transparent 0), linear-gradient(#ddd 1rem, transparent 0), linear-gradient(#ddd 1rem, transparent 0);
  height: 34rem; }
  .panel-loader::before {
    content: "";
    display: block;
    position: relative;
    width: 220%;
    height: 2rem;
    left: 0;
    top: -3rem;
    background-position: 0.25rem -1.75rem;
    background-size: 40% 100%;
    animation: loading 2s infinite;
    background-repeat: repeat-y;
    background-image: linear-gradient(#ddd 2rem, transparent 0); }

.side-panel-loader {
  position: relative;
  background-position: 2.25rem 2.25rem, 2.25rem 4.25rem, 2.25rem 6.25rem, 2.25rem 8.25rem, 2.25rem 10.25rem;
  background-size: 50% 100%, 0 100%, 70% 100%, 80% 100%, 30% 100%;
  animation: loading 2s infinite;
  background-repeat: repeat-y;
  background-image: linear-gradient(#ddd 2em, transparent 0), linear-gradient(#ddd 1rem, transparent 0), linear-gradient(#ddd 1rem, transparent 0), linear-gradient(#ddd 1rem, transparent 0), linear-gradient(#ddd 1rem, transparent 0);
  height: 16rem;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.18), 0 1px 3px rgba(0, 0, 0, 0.12);
  @apply bg-white; }

/** Variables **/
.components-editor-notices__dismissible {
  position: sticky;
  top: 0;
  right: 0;
  color: #1a1a1a; }

.components-editor-notices__pinned {
  position: relative;
  left: 0;
  top: 0;
  right: 0;
  color: #1a1a1a; }

.components-editor-notices__dismissible .components-notice,
.components-editor-notices__pinned .components-notice {
  box-sizing: border-box;
  margin: 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  padding: 0 12px;
  min-height: 60px; }
  .components-editor-notices__dismissible .components-notice .components-notice__dismiss,
  .components-editor-notices__pinned .components-notice .components-notice__dismiss {
    margin-top: 12px; }

.components-editor-notices__snackbar {
  width: 100%;
  position: fixed;
  bottom: 10px;
  right: 10px; }
  @media (min-width: 782px) {
    .components-editor-notices__snackbar {
      width: fit-content;
      width: -moz-fit-content; } }

/* Main Tabs */
.sd-settings-tabs .components-tab-panel__tabs-item.is-active {
  @apply font-bold; }

.sd-settings-tabs .components-tab-panel__tabs-item {
  @apply font-normal; }

.sd-settings-tabs {
  box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 3px 0px, rgba(0, 0, 0, 0.14) 0px 1px 1px 0px, rgba(0, 0, 0, 0.12) 0px 2px 1px -1px;
  height: fit-content; }

.sd-settings-tabs > .components-tab-panel__tabs {
  border-bottom: 1px solid #e2e4e7; }

#tab-panel-0-general-view,
#tab-panel-0-search-view {
  @apply p-4; }

/* BaseControl Label Fields */
.components-base-control__label {
  @apply text-sm;
  @apply font-semibold;
  margin-bottom: 0 !important; }

.components-base-control__help {
  @apply text-xs;
  @apply text-gray-500;
  margin-top: 0.25rem !important; }

.inline-number-control .components-base-control__field {
  @apply flex;
  align-items: center; }

.inline-number-control .components-base-control__label {
  margin-bottom: 0 !important; }

/* Override Default Content Padding */
#wpcontent {
  padding-left: 0; }

/* Layout Tab */
.sd-layout-settings-tabs .components-tab-panel__tabs {
  @apply flex-col;
  background-color: #f5f5f5; }

.sd-layout-settings-tabs .components-tab-panel__tabs-item.is-active {
  box-shadow: none !important;
  background-color: #fff;
  border-left: 5px solid var(--wp-admin-theme-color);
  color: var(--wp-admin-theme-color); }

.sd-layout-settings-tabs .components-tab-panel__tabs button {
  @apply text-center;
  @apply whitespace-no-wrap; }

.sd-layout-settings-tabs .components-tab-panel__tab-content {
  @apply w-full;
  @apply p-5; }

/* Input Control Search */
.components-select-control__input {
  height: 16rem !important; }
